//import 'regenerator-runtime/runtime'
import React, {useEffect, useState} from 'react'
import "bootstrap/dist/css/bootstrap.min.css";
import {Navbar, Nav, Container, Button} from 'react-bootstrap';
import { BrowserRouter as Router, Switch, Route, Routes, Link, useNavigate } from "react-router-dom";

import Register from "./components/Register"
import Upload from "./components/Upload"
import Login from "./components/Login"


export default function App() {

    const navigate = useNavigate();
    const handleLogout = () => {
        sessionStorage.removeItem('Auth Token');
        setToken("")
        navigate('/login')
    }

    const [token, setToken] = useState("")

    useEffect(() => {
        let authToken = sessionStorage.getItem('Auth Token')
        setToken(authToken)
        console.log("authToken:", authToken, token)
    })

    return(
        <div>
            <Navbar style={{margin: 0}} collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand href="/">PhacoTrainer</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        {token == null ? 
                            <Nav className="justify-content-end" style={{ width: "100%" }}>
                                <Nav.Link href="/register">Register</Nav.Link>
                                <Nav.Link href="/login">Login</Nav.Link>
                            </Nav>
                            :
                            <Nav className="justify-content-end" style={{ width: "100%" }}>
                                <Nav.Link href="/">Upload</Nav.Link>
                                <Nav.Link onClick={handleLogout}>Logout</Nav.Link>
                            </Nav>
                        }
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <Routes>
                <Route path="/" exact element={<Upload/>} />
                <Route path="/register" exact element={<Register/>} />
                <Route path="/login" exact element={<Login/>} />
                
            </Routes>
        </div>
    )
  
}
