import React, {useState, useEffect} from 'react'
import {
    Container,
    Form, 
    Button,
    Row,
    Col
} from 'react-bootstrap';
import { getStorage, ref, uploadBytes } from "firebase/storage";
import { getFirestore, collection, addDoc, getDocs } from "firebase/firestore"; 
import { FilePlus, Check, XCircle } from 'react-bootstrap-icons'
import { getAuth, onAuthStateChanged } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import { Bars } from  'react-loader-spinner'

const Upload = () => {

    const [file, setFile] = useState("")
    const storage = getStorage()
    const db = getFirestore()
    const [videos, setVideos] = useState([])
    const [usid, setUsid] = useState("")
    const [isActive, setIsActive] = useState(false)
    const [fs, setFS] = useState(false)

    const navigate = useNavigate();

    const fetchVideos = async () => {
        const querySnapshot = await getDocs(collection(db, "videos"));
        let data = null;
        querySnapshot.forEach((doc) => { setVideos(videos => [...videos, doc.data()]) });
        setUsid(sessionStorage.getItem('localId'))
    }

    useEffect(() => {

        let authToken = sessionStorage.getItem('Auth Token')
        console.log("login-auth:", authToken)
        if (authToken) {
            navigate('/')
        }

        if (!authToken) {
            navigate('/login')
        }

        setVideos([])
       
        fetchVideos();
    }, [])

    const uploadFile = async (e) => {
        e.preventDefault();
        
        let date = new Date().getTime() + parseInt( 100000 * Math.random() );
       
        try{
            
            let format = file.name.toString().split(".")
            format = format[format.length -1]
            
            let fileName = `${date}.${format}`

            let uid = sessionStorage.getItem('localId');
            const auth = getAuth();
            onAuthStateChanged(auth, async (user) => { 
                uid = user.uid 
            })
            

            const docRef = await addDoc(collection(db, "videos"), {
                fileName: fileName,
                creationDate: new Date(),
                uid: uid,
                videold: ''
            });
            
            const videoRef = ref(storage, `${uid}/${fileName}`);
            uploadBytes(videoRef, file).then((snapshot) => {
                console.log('Uploaded a blob or file!');
            });

            window.location.reload()

        }catch(error){
            console.log("Error:", error)
        }
    }

    const cancelUpload = () => {
        setFile("")
        setFS(false)
    }

    return (
        
            <Container style={{marginTop: '2em'}}>
                <h5>New Video</h5>
                <Row>
                    <Col sm={12} md={1}>
                        <div style={{
                            display: 'flex',
                            margin: 'auto',
                            width: 400,
                            flexWrap: 'wrap',
                        }}>
                            
                            <Form onSubmit={uploadFile}>
                                <Form.Group>
                                    <input id="icon-button-file"
                                        type="file" style={{ display: 'none' }} onChange={ (e)=>{setFile(e.target.files[0]); setFS(true);} } />
                                    <label htmlFor="icon-button-file">
                                        <FilePlus size={120}/>
                                    </label>
                                </Form.Group>
                                <br/>
                                {fs? 
                                <Row>
                                    <Col sm={6} md={1}>
                                        <Button variant="success" type="submit">
                                            <Check />
                                        </Button>
                                    </Col>
                                    <Col sm={6} md={1}>
                                        <Button variant="danger" onClick={cancelUpload} >
                                            <XCircle />
                                        </Button>
                                    </Col>
                                </Row> : null
                                }
                            </Form>
                        </div>
                    </Col>
                    <Col md={11} />
    
                </Row>

                <Row style={{marginTop: '2em'}}>
                    
                        <h5>Videos</h5>
                        {
                            videos && videos.map(video => {
                                return(
                                    <Col sm={12} md={4} key={video.fileName}>
                                        <video width="250" height="240" 
                                            src={ `https://firebasestorage.googleapis.com/v0/b/video-uploader-ae0a3.appspot.com/o/${video.uid}%2F${video.fileName}?alt=media`} 
                                            controls>
                                            Your browser does not support the video tag.
                                        </video>
                                    </Col>  
                                )
                            })
                        }
                    
                </Row>
            </Container>
        
    )
}

export default Upload;